@import '../../sass/Custom.bootstrap.scss';

    .my-toolbar {
        position: absolute;
        z-index: 9;
        background-color: #f1f1f1;
        text-align: center;
        border: 1px solid #d3d3d3;
        .my-body {
            background-color: #a0a0f0;
        }
        .my-drag-selector {
            padding: 10px;
            cursor: move;
            z-index: 10;
            background-color: #2196F3;
            color: #fff;
            &:hover {
                @extend .my-focus;
            }
        }
    }
    